body {
  font-family: Arial, sans-serif;
  text-align: center;
  background-image: url('TAN\ Tangkiwood\ \(1\).png');
  background-size: cover;
  margin: 0;
  padding: 20px;
}

.container {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  transition: 0.2s;
  background-color: rgba(0, 0, 0, 0.904);
}

.container:hover{
  max-width: 700px;
  margin: auto;
  background-image: url();
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  transition: 0.2s;
}

.heart-container {
  text-align: center;
  font-size: 24px;
  margin-top: 20px;
  color: #ffffff; 
  border-radius: 15px;
  background-image: url(download\ \(1\).jpeg); 
  background-size: cover;
  animation: moveBackground 4s ease-in-out infinite; 
  z-index: -1; 
}

@keyframes moveBackground {
    0% { background-position: 0% 0%; }
    50% { background-position: 0% 100%; } /* Mova para baixo */
    100% { background-position: 0% 0%; } /* Volte para cima */
}


input {
  padding: 10px;
  margin: 5px;
  width: calc(100% - 22px);
  border-radius: 15px;
  transition: .2s;
}

input:hover{
  transform: scale(1);
  padding: 15px;
  margin: 5px;
  width: calc(100% - 22px);
  border-radius: 15px;
}

button {
  color: aliceblue;
  padding: 10px;
  margin: 5px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  background-color: #28a745;
}
button:hover{
  background-color: darkgreen;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin: 5px 0;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
}